import React, { useState, useEffect } from 'react';
import app from '../firebase_config';
import { getDatabase, ref, get, remove } from 'firebase/database';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';
import ReactPaginate from 'react-paginate';
import { format, parseISO, isValid } from 'date-fns';
import { parsePhoneNumberFromString } from 'libphonenumber-js';

const formatPhone = (phone) => {
  const parsedPhone = parsePhoneNumberFromString(phone, 'VN');
  return parsedPhone ? parsedPhone.formatInternational() : phone;
};

const formatDate = (date) => {
  return isValid(parseISO(date)) ? format(parseISO(date), 'dd-MM-yyyy') : date;
};

function ManageBill() {
  const navigate = useNavigate();
  const [billArray, setBillArray] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [currentPage, setCurrentPage] = useState(0);
  const itemsPerPage = 5;

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    const db = getDatabase(app);
    const dbRef = ref(db, "bill");
    const snapshot = await get(dbRef);
    if (snapshot.exists()) {
      const myData = snapshot.val();
      const temporaryArray = Object.keys(myData).map(myFireId => {
        return {
          ...myData[myFireId],
          bill_id: myFireId
        };
      });
      setBillArray(temporaryArray);
    }
  };

  const deleteBill = async (bill_id) => {
    const result = await Swal.fire({
      title: 'Bn c chc chn?',
      text: "Bn s khng th khi phc hnh ng ny!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      cancelButtonText: 'Hy',
      confirmButtonText: 'Xa n!'
    });

    if (!result.isConfirmed) {
      return; // Ngi dng khng xc nhn xa, kt thc hm
    }

    const db = getDatabase(app);
    const dbRef = ref(db, "bill/" + bill_id);
    await remove(dbRef);

    // Cp nht li mng billArray
    setBillArray(prevArray => prevArray.filter(item => item.bill_id !== bill_id));
    if (currentPage > 0 && currentPageData.length === 1) {
      setCurrentPage(currentPage - 1); // Li trang hin ti nu xa bn ghi cui cng
    }
  };

  const handleSearchChange = (event) => {
    setSearchTerm(event.target.value);
  };

  const filteredBills = billArray.filter(item =>
    item.customer_name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const offset = currentPage * itemsPerPage;
  const currentPageData = filteredBills.slice(offset, offset + itemsPerPage);
  const pageCount = Math.ceil(filteredBills.length / itemsPerPage);

  const handlePageClick = (event) => {
    setCurrentPage(event.selected);
  };

  return (
    <div>
      <div className="header">Danh sch ha n</div>
      <input className="search"
        type="text"
        placeholder="Tm kim..."
        value={searchTerm}
        onChange={handleSearchChange}
      />
      <button className="writeBtn" onClick={() => navigate("/writeBill")}>To ha n</button>
      <button className="productBtn" onClick={() => navigate("/")}>Sn phm</button>

      <table>
        <thead>
          <tr>
            <th>Tn khch hng</th>
            <th>S in thoi</th>
            <th>a ch</th>
            <th>Ngy</th>
            <th>Trng thi</th>
            <th>Ghi ch</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {currentPageData.map((item, index) => (
            <tr key={index}>
              <td>{item.customer_name}</td>
              <td>{formatPhone(item.phone)}</td>
              <td>{item.address}</td>
              <td>{formatDate(item.date)}</td>
              <td>{item.status ? ' thanh ton' : 'Cha thanh ton'}</td>
              <td>{item.note}</td>
              <td>
                <button className="editBtn" onClick={() => navigate(`/editBill/${item.bill_id}`)}>Sa</button>
                <button className="deleteBtn" onClick={() => deleteBill(item.bill_id)}>Xa</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <ReactPaginate
        previousLabel={" Previous"}
        nextLabel={"Next "}
        pageCount={pageCount}
        onPageChange={handlePageClick}
        containerClassName={"pagination"}
        previousLinkClassName={"pagination__link"}
        nextLinkClassName={"pagination__link"}
        disabledClassName={"pagination__link--disabled"}
        activeClassName={"pagination__link--active"}
      />
    </div>
  );
}

export default ManageBill;
